


import { useSelector } from "react-redux";
import classes from './rule34.module.css';
import React from 'react'





const Rule34 = function(){


    let items = useSelector(state => state.counter.items)

    return(
        <div className={classes.wrapper}>
            
            
                {items.map(item => 
                item.file_url.endsWith('.mp4')

                ? <div className={classes.container} style={{height:item.height, width: item.width}} > <video v-if={item.file_url} className={classes.img} style={{height:item.height, width: item.width}} src={`https://api-cdn-us-mp4.rule34.xxx/images/${item.directory}/${item.image}`} controls type='text/html'> </video></div>
                :

                item.file_url.endsWith('.gif')
        
                ? <div className={classes.container} style={{height:item.height, width: item.width}} > <img className={classes.img} style={{height:item.height, width: item.width}} src={item.sample_url} alt={item.sample_url} loading='lazy'/> </div>
                : 

                item.sample_url.endsWith('.jpg')
                ? <div className={classes.container}  > <img className={classes.img} style={{height:item.sample_height, width: item.sample_width}} src={item.sample_url} alt={item.sample_url} loading='lazy'/> </div>
                :
                item.sample_url.endsWith('.jpeg')
                ? <div className={classes.container} > <img className={classes.img} style={{height:item.height, width: item.width}} src={item.sample_url} alt={item.sample_url} loading='lazy'/> </div>
                :
                item.sample_url.endsWith('.png')
                ? <div className={classes.container}  > <img className={classes.img} style={{height:item.sample_height, width: item.sample_width}} src={item.sample_url} alt={item.sample_url} loading='lazy'/> </div>
                : null

                )}
                
        </div>
        
    )
}

export default Rule34;