import React, { useState } from "react";
import Description from "../description/description";
import classes from './counter.module.css';
import { useDispatch, useSelector } from "react-redux";
import { actions } from "../../store/counterReducer";


const Counter = function(props){
    debugger
    const dispatcher = useDispatch()
    const number = useSelector(state => state.counter.count)

    // const [count, setCount] = useState(props.countStart || 0)
    const [ color, setColor ] = useState('#000');
    const [ displayInc, setDisplayDec] = useState('none')
    const [ displayDec, setDisplayInc] = useState('none')

    async function timeout(delay) {
        return new Promise( res => setTimeout(res, delay) );
    } 

    async function increment(){
        setColor('#32a852')
        dispatcher({type: actions.increment})
        setDisplayInc('block')
        await timeout(200)
        setColor('#000')
        setDisplayInc('none')
    }

    async function decrement(){
        setColor('#fa0000')
        dispatcher({type: actions.decrement})
        setDisplayDec('block')
        await timeout(200)
        setColor('#000')
        setDisplayDec('none')
        
    }
    function reset(){
        dispatcher({type: actions.resetCounter})
        
    }

    return (
        <div className={classes.wrapper}>
            <div className={classes.header}> 
                    <h4 className={classes.decrease} style={{'display': displayInc}}>-1</h4>
                    <h1 style={{'color':color}}>{number}</h1>
                    <h4 className={classes.increase} style={{'display': displayDec}}>+1</h4>
            </div>

            <div className={classes.buttons}>
                <button className={classes.button} onClick={decrement}>-</button>
                <button className={classes.button} onClick={increment}>+</button>
            </div>
            <button className={classes.reset} onClick={reset}>reset</button>

            <Description description='A simple counter with three buttons - increase,decrease and reset'/>
        </div>
    )
}


export default Counter;