

export const actions = {
    searchRule34 : 'SEACRHRULE34',
    increment : 'INCREMENT',
    decrement : 'DECREMENT',
    resetCounter: 'RESETCOUNTER',
    addNote: 'ADDNOTE',
    deleteNote: 'DELETENOTE',
    searchWeather : 'SEARCHWEATHER',
    resetRule34 : 'RESETRULE34',
    weatherError: 'WEATHERERROR'
}


const defaultStore = {
    count : 10,
    tags: '',
    items: [],
    notes : [],
    weather : [],
    city : '',
    country: '',
    isWeatherError: false
        
    
}

export let counterReducer = (state = defaultStore, action) =>{

switch (action.type){
    
    case actions.resetRule34:
        return{...state, items: []}

    case actions.searchRule34:
        return {...state, items: action.items}

    case actions.increment:
        return {...state, count: state.count + 1}

    case actions.decrement:
        return {...state, count: state.count - 1}
    
    case actions.resetCounter:
        return{...state, count: 0}
    
    case actions.addNote:
        return {...state, notes: [...state.notes, action.payload]}
    
    case actions.searchWeather:
        return {...state, weather: action.weather, city: action.city, country: action.country}
    
    case actions.weatherError:
        return{...state, weather: [], isWeatherError: actions.isError}

    default:
        return state

}
}





