import { useState } from "react"
import React from "react"
import { useDispatch } from "react-redux"
import { actions } from './../../../store/counterReducer'
import { useNavigate } from "react-router"
import Description from "../../description/description"
import classes from './weatherSearch.module.css'

export default WeatherSearch => {

    const capitalize = s => s && s[0].toUpperCase() + s.slice(1)

    const dispatch = useDispatch()
    const navigate = useNavigate()
    let isError = false

    let apicall = async function(city) {await fetch(`http://api.weatherapi.com/v1/forecast.json?key=c7847264cddd4ea0a63121822222606&q=${capitalize(city)}&days=5&aqi=no&alerts=no`)
    .then(res => res.json())
    .then(result => 
    {
        dispatch({type:actions.searchWeather, weather: result.forecast.forecastday, city: result.location.name, country: result.location.country})

    }).catch(()=>
    {
        navigate('/weather/error') 
        isError = true
    })
    }

    const [city, setCity] = useState('')
    

    const cityHandler = (e) => {
        setCity(e.target.value)
    }

    const buttonHandler = () => {
        apicall(city)
        debugger
        if (isError === false){
            navigate('/weather/view')
        }
    }
    
    
    return(
        <>
        <Description description='Simple forecast app for 3 days (free api limit)' />
        <div className={classes.wrapper}>
            
            <input className={classes.input} type='text' placeholder="Enter city" onChange={ cityHandler } value={city}/>
            <button className={classes.button} onClick={ buttonHandler }>Search</button>
        </div>
        </>
        
    )
}