
import classes from './header.module.css';
import { NavLink } from "react-router-dom";
import React from 'react'



const Header = () =>{
    return(
        <ul className={classes.nav}>

            <li className={classes.link}><NavLink className={({ isActive }) => (isActive ? classes.active : classes.link)} to="/">Home</NavLink></li>
            <li className={classes.link}><NavLink className={({ isActive }) => (isActive ? classes.active : classes.link)} to="/counter">Counter</NavLink></li>
            <li className={classes.link}><NavLink className={({ isActive }) => (isActive ? classes.active : classes.link)} to="/calculator">Calculator</NavLink></li>
            <li className={classes.link}><NavLink className={({ isActive }) => (isActive ? classes.active : classes.link)} to="/rule34/search">Rule34</NavLink></li>
            <li className={classes.link}><NavLink className={({ isActive }) => (isActive ? classes.active : classes.link)} to="/notes">Notes</NavLink></li>
            <li className={classes.link}><NavLink className={({ isActive }) => (isActive ? classes.active : classes.link)} to="/weather/search">Weather</NavLink></li>
        </ul>
    )
}

export default Header;