
import React from "react"

import classes from './weatherView.module.css'

import { useSelector } from "react-redux";

export default WeatherView =>{

    let weather = useSelector(state => state.counter.weather)
    let city = useSelector(state => state.counter.city)
    let country = useSelector(state => state.counter.country)
    let isError = useSelector(state => state.counter.isWeatherError)

    if (isError !== true){

        return(
            <div>
                
                <div className={classes.header}>3-day forecast</div>
    
                <h1 className={classes.city}>{city},{country}</h1>
    
                <div className={classes.forecast}>
                    {weather.map(item =>{
                        var timestamp = Date.parse(item.date);
                        var dateObject = new Date(timestamp).toLocaleTimeString('en-us', {  weekday: 'long', month: 'long', day: 'numeric'});
                        return(
                        <div className={classes.item}> 
                            <h3 className={classes.date}>{dateObject}</h3>
                            <img className={classes.logo} src={item.day.condition.icon} alt={item.day.condition.icon}/>
                            <h3 className={classes.temp}>{`${item.day.avgtemp_c}°`}</h3>
                            <h4 className={classes.description}>{item.day.condition.text}</h4>
                        </div>
                        )
                    })}
    
                </div>
            </div>
        )
    }
    else{
            return(
                <div className={classes.error}>
                        City not found
                </div>
            )
        }
    }
    
