
import { actions } from "../../../store/counterReducer";
import { useState } from "react";
import { useDispatch } from "react-redux";
import classes from './Rule34search.module.css';
import React from 'react'
import { useNavigate } from "react-router-dom";
import { NavLink } from "react-router-dom";
import Switch from "../../decorative/switch/switch";


const Rule34Search = function(){

    const [value, setValue] = useState(false);
    let dispatch = useDispatch()

    let apicall = async function(tags, limit) {await fetch(`https://api.rule34.xxx//index.php?page=dapi&s=post&q=index&tags=${tags}&limit=${limit}&json=1`)
    .then(res => res.json()).then(result => dispatch({type:actions.searchRule34,items: result}))}

    const [tags, settags] = useState('');
    const [limit, setlimit] = useState('');
    const navigate = useNavigate()


    const handleChangeTags = event => {
        settags(event.target.value);
    }

    const handleChangeLimit = event => {
        setlimit(event.target.value);
    }

    let searchfunc = () =>{
        dispatch({type:actions.resetRule34})
        apicall(tags, limit)
        
        navigate('/rule34/pics')
        
    }
    return(
        <div>
            <h1>rule34</h1>
                <input placeholder="Write tags" value={tags} onChange={handleChangeTags}></input>
                <input placeholder="Choose limit of photos" value={limit} onChange={handleChangeLimit}></input>

                <Switch isOn={value} handleToggle={() => setValue(!value)}/>

                <NavLink className={({ isActive }) => (isActive ? classes.active : classes.link)} to="/rule34/pics" onClick={ searchfunc }>Search</NavLink>
        </div>
    )
}

export default Rule34Search;