import React from 'react';

import Counter from './components/counter/counter';
import HomePage from './components/home/home';
import Header from './components/header/header';
import Calculator from './components/calculator/calculator'
import Rule34 from './components/rule34/rule34';
import Notes from './components/notes/notes';
import Rule34Search from './components/rule34/search/Rule34search';

import './styles/App.css';


import { BrowserRouter, Routes, Route } from "react-router-dom";
import WeatherView from './components/weather/weatherView/weatherView';
import WeatherSearch from './components/weather/weatherSearch/weatherSearch';
import WeatherError from './components/weather/weatherError/weatherError';



function App() {
  return (
  <>
      <BrowserRouter>
        <Header />
          <Routes>
            <Route path="/" element={ <HomePage /> } />
            <Route path="counter" element={ <Counter countStart={0} /> } />
            <Route path="calculator" element={ <Calculator /> } />
            <Route path="rule34/search" element={ <Rule34Search /> } />
            <Route path="rule34/pics" element= { <Rule34 /> } />   
            <Route path="notes" element= { <Notes /> }/>     
            <Route path="weather/view" element= { <WeatherView /> }/>
            <Route path="weather/search" element= { <WeatherSearch /> }/>   
            <Route path="weather/error" element= { <WeatherError /> }/>   
          </Routes>
        </BrowserRouter>
  </>
);
}


export default App;
